@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=PT+Sans+Narrow:400,700");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

.icon-facebook { width: 0.53em; height: 1em; fill: #000; }

/* Colors */
body { padding: 0; margin: 0; font-family: 'PT Sans Narrow', sans-serif; font-weight: 700; font-size: 16px; color: #333743; }

a { text-decoration: none; color: #9297a3; }

a:active, a:hover { color: #333743; }

/* .wrapp max-width: 1440px margin: 0 auto */
section { max-width: 1440px; margin: 0 auto; padding: 0 40px; }

.btn-mobile { display: none; }

.header { display: flex; flex-wrap: wrap; align-items: center; padding: 0 40px; box-shadow: 0 12px 9px -10px rgba(189, 189, 189, 0.5); }

@media only screen and (max-width: 500px) { .header { padding: 0 20px; } }

.header__logo { margin-right: 70px; padding-top: 20px; padding-bottom: 20px; }

@media only screen and (max-width: 899px) { .header__logo { margin-right: 10px; } }

.header__nav { list-style: none; padding: 0; }

@media only screen and (max-width: 899px) { .header__nav { order: 1; display: block; width: 100%; text-align: center; } }

@media only screen and (max-width: 500px) { .header__nav { display: none; } }

.header__nav li { display: inline-block; margin-right: 57px; }

@media only screen and (max-width: 899px) { .header__nav li { margin-right: 9%; } }

@media only screen and (max-width: 500px) { .header__nav li { display: block; width: 100%; margin-bottom: 5px; margin: 0 auto; transition: all 0.3s; }
  .header__nav li:hover { background-color: #000; } }

.header__nav li a { text-transform: uppercase; }

.header__bag { flex-grow: 1; text-align: right; padding-right: 30px; border-right: 1px solid #dedede; line-height: 44px; }

@media only screen and (max-width: 899px) { .header__bag { border: none; padding-right: 0; } }

@media only screen and (max-width: 500px) { .header__bag { flex-grow: 0; } }

.header__bag span { font-family: Georgia, sans-serif; font-weight: 400; font-size: 12px; color: #9297a3; display: inline-block; width: 16px; height: 16px; margin-left: 13px; line-height: 15px; border-radius: 50%; text-align: center; background-color: #333743; color: #fff; }

.header__form { margin-left: 37px; }

@media only screen and (max-width: 500px) { .header__form { margin: auto; } }

.header__form-login { padding-right: 19px; border-right: 2px solid #000; }

@media only screen and (max-width: 500px) { .header__form-login { padding-right: 5px; } }

.header__form-sign { padding-left: 17px; }

@media only screen and (max-width: 500px) { .header__form-sign { padding-left: 5px; } }

@media only screen and (max-width: 500px) { .header .btn-mobile { display: block; background-color: transparent; outline: none; padding: 5px 10px; border: 2px solid #333743; border-radius: 5px; margin-left: 5px; cursor: pointer; } }

/* End header */
.content { display: flex; flex-wrap: wrap; }

@media only screen and (max-width: 1139px) { .content { justify-content: center; } }

.content__photo { display: flex; }

.content__photo-nav { list-style: none; margin-right: 10px; margin-top: 50px; }

.content__photo-nav li.select { border-right: 3px solid #dedede; }

@media only screen and (max-width: 599px) { .content__photo-nav a > img { min-width: 50px; } }

.content__photo-panels { margin-top: 50px; }

@media only screen and (max-width: 599px) { .content__photo-panels > div > img { max-width: 200px; } }

.content__description { max-width: 500px; margin: 0 auto; text-align: center; margin-top: 50px; padding-left: 20px; padding-right: 20px; }

.content__description-link { font-family: Georgia, sans-serif; font-weight: 400; font-size: 12px; color: #9297a3; color: #9fcede; }

.content__description-title { text-transform: uppercase; margin-bottom: 62px; }

.content__description-price { font-family: Georgia, sans-serif; font-weight: 400; font-size: 12px; color: #9297a3; font-size: 22px; }

.content__description-subtitle { margin-top: 62px; padding-bottom: 6px; border-bottom: 1px solid #dbdee5; }

.content__description-description { font-family: Georgia, sans-serif; font-weight: 400; font-size: 12px; color: #9297a3; margin-top: 22px; }

.content__description-colors { max-width: 150px; text-align: left; padding-bottom: 6px; border-bottom: 1px solid #dbdee5; margin-top: 70px; }

.content__description fieldset { border: none; text-align: left; padding: 0; margin: 0px; }

.content__description fieldset input[type="radio"] { appearance: none; border: 1px solid #9297a3; outline: none; height: 12px; width: 12px; border-radius: 50%; background-color: #fff; margin-left: -25px; margin-right: 10px; cursor: pointer; position: relative; }

.content__description fieldset input[type="radio"]:hover { background-color: #9297a3; }

.content__description fieldset input[type="radio"]:checked { border: 1px solid #000; background-color: #000; }

.content__description fieldset input[type="radio"]:checked::before { content: '✔'; position: absolute; height: 12px; width: 12px; line-height: 12px; color: #fff; }

.content__description label { cursor: pointer; }

.content__description label span { display: inline-block; width: 12px; height: 12px; margin-right: 10px; }

.content__description label .square-blue { background-color: #9fcede; padding-right: 10px; }

.content__description label .square-brown { background-color: #333743; }

.content__description button { outline: none; border: none; font-family: Georgia, sans-serif; font-weight: 400; font-size: 12px; color: #9297a3; font-size: 13px; color: #fff; width: 100%; border-radius: 2px; background-color: #afa394; cursor: pointer; padding-top: 20px; padding-bottom: 15px; margin-top: 70px; }

.content__description button:hover { background-color: #8d7d69; }

/* end content*/
.product { padding-left: 40px; padding-right: 40px; margin-top: 117px; margin-bottom: 50px; }

.product__title { font-size: 16px; text-align: center; text-transform: uppercase; margin-bottom: 22px; }

.product__title small { font-family: Georgia, sans-serif; font-weight: 400; font-size: 12px; color: #9297a3; display: block; text-transform: none; }

.product__items { display: grid; grid-template-columns: repeat(auto-fill, 230px); grid-gap: 20px; justify-content: space-around; }

.product__items .photo { width: 240px; height: 255px; line-height: 460px; background-color: #f5f5f5; text-align: center; }

.product__items-item h4 { display: flex; justify-content: space-between; text-transform: uppercase; line-height: 0; padding-left: 15px; margin: 20px 0 5px 0; max-width: 210px; }

.product__items-item span { font-family: Georgia, sans-serif; font-weight: 400; font-size: 12px; color: #9297a3; padding-left: 15px; }

/* end product */
